import Vue from "vue";
import Modal from "./Modal.vue";

export default Vue.extend({
  components: {
    modal: Modal,
  },
  data() {
    return {
      test: "board",
      loginShow: false,
    };
  },
  methods: {
    modalOnOff(): void {
      this.loginShow = !this.loginShow;
    },
  },
});
